	import MxDatePicker from '../../../components/mx-datepicker/mx-datepicker.vue'
    import dateutll from '../../../common/util.js'
	export default {
		
	components: {
		MxDatePicker
	},
	data() {
			return {
				inputresean:'',
				beizhusean:'',
				haverull:false,
				textnum:'0/30',
				beizhunum:'0/30',
				// 时间选择器需要的数据
				showPicker: false,
				date: '2019/01/01',
				type: 'date',
				value: '',
				time: '15:00:12',
				//关于拍照和从相册选择图片
				imageList: [],
				// 				sourceTypeIndex: 2,
				// 				sourceType: ['拍照', '相册', '拍照或相册'],
				// 				sizeTypeIndex: 2,
				// 				sizeType: ['压缩', '原图', '压缩或原图'],
				countIndex: 8,
				count: [1, 2, 3, 4, 5, 6, 7, 8, 9],
				base64:'',
				// 分配周期
				renwuzhouqi:'每天'
				
			};
		},
		onReady(){
		
			this.date=dateutll.dateUtils.getNowFormatDate()
			
		},
		onUnload() {
					this.imageList = [],
		// 				this.sourceTypeIndex = 2,
		// 				this.sourceType = ['拍照', '相册', '拍照或相册'],
		// 				this.sizeTypeIndex = 2,
		// 				this.sizeType = ['压缩', '原图', '压缩或原图'],
						this.countIndex = 8;
				},
		methods:{
			inputholder:function(e){
				let length=this.inputresean.length;
				this.textnum=length+'/'+'30'
			},
			inputholder2:function(e){
				let length=this.beizhusean.length;
				this.beizhunum=length+'/'+'30'
			},
			switchchange:function(e){
				//修改选项bar
				 this.haverull=e.target.value;
			},
			choisetime:function(type){
				//弹出时间的选择框
				this.type = type;
				this.showPicker = true;
				this.value = this[type];
			},
			onSelected(e) {//选择
				this.showPicker = false;
				if(e) {
					this[this.type] = e.value; 
				}
			},
			addimage:function(){
				// 添加图片
				if (this.imageList.length === 9) {
					 uni.showToast({
					 	title: '最多上传九张照片',
					 	mask: false,
					 	duration: 1500,
						icon:"none"
					 });
					 return
				}
				uni.chooseImage({
					sourceType: ['camera', 'album'],
					sizeType: ['compressed'],
					count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length : this.count[this.countIndex],
					success: (res) => {
						this.imageList = this.imageList.concat(res.tempFilePaths);
					}
				})
				
			},
			showbigimage:function(e){
				// 展示大图
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			choisezhouqi:function(){
				// 添加任务周期
				let that =this;
				uni.showActionSheet({
					itemList: ['每天', '每周', '每月'],
						success: function (res) {
						var	renwulist=['每天', '每周', '每月'];
						that.renwuzhouqi=renwulist[res.tapIndex]
					},
					fail: function (res) {
					console.log(res.errMsg);
				}
				});
			},
			addshenqing:function(e){
				// 提交任务按钮
			}
		}
	}
